<template>
  <div class="content-container">
    <div class="l-content">
      <el-button
        style="margin-right: 20px"
        @click="handleMenu"
        icon="el-icon-menu"
        size="mini"
      ></el-button>
      <!-- 面包屑 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item
          v-for="item in tags"
          :key="item.path"
          :to="{ path: item.path }"
          >{{ item.label }}</el-breadcrumb-item
        >
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown @command="handleClick">
        <img class="image-top" src="../assets/img/touxiang.jpg" alt="" />

        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item  command="cancel">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
  
<script>
import Cookie from "js-cookie";
import { mapState } from "vuex";
// import { format } from "echarts";/
export default {
  data() {
    return {};
  },
  methods: {
    handleMenu() {
      this.$store.commit("collapseMenu");
    },
    handleClick(command) {
      //清除cookie中的token
      if (command === 'cancel') {
        Cookie.remove('token');
        Cookie.remove('menu');
        this.$router.push("/login");
      }
    },
  },
  computed: {
    ...mapState({
      tags: (state) => state.tab.tabsList,
    }),
  },
};
</script>
<style lang="less" scoped>
.content-container {
  padding: 0 20px;
  background-color: black;
  height: 60px;
  display: flex;
  // justify-content: space-between;
  justify-content: space-between;
  align-items: center;

  .text {
    color: #fff;
    font-size: 14px;
    margin: 10px;
  }

  .r-content {
    margin: 0;

    .image-top {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin: 0;
    }
  }
  .l-content {
    display: flex;
    align-items: center;
    /deep/.el-breadcrub__item {
      .el0breadcrumb__inner {
        font-weight: normal;
        &.is-link {
          color: #666;
        }
      }
      &:last-child {
        .el0breadcrumb__inner {
          color: #fff;
        }
      }
    }
  }
}
</style>